/* workintro */
.work-intro {
    margin: 50px 0;
    height: auto;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-auto-rows: minmax(120px, auto);
    column-gap: 30px;
    grid-row-gap: 70px;
    grid-template-areas:
        "info"
        "img"
        "statement";
}

.work-intro-info {
    margin-top: 50px;
    position: relative;
    grid-area: info;
}

.work-intro-info__label {
    color: #ff7c7c;
    font-size: 2rem;
}

.work-intro-info__title {
    font-size: 4rem;
}

.work-intro-info-details {
    font-size: 1.5rem;
    margin-top: 50px;
    display: grid;
    width: 100%;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 20px;
    grid-row-gap: 30px;
    grid-template-areas:
        "date link"
        "field client";
}

.work-intro-info-details__date {
    grid-area: date;
}

.work-intro-info-details__link {
    grid-area: link;
}

.work-intro-info-details__link a{
    position: relative;
}
.work-intro-info-details__link a:before{
    content: '';
    width: 100%;
    height: 5px;
    background: rgba(0, 255, 153, 0.5);
    position: absolute;
    bottom: 2px;
    z-index: -1;
}

.work-intro-info-details__field {
    grid-area: field;
}

.work-intro-info-details__client {
    grid-area: client;
}

.work-intro-img-wrap {
    grid-area: img;
}

.work-intro-img-wrap figure{

}

.work-intro-img {
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
}

.work-intro-statement {
    grid-area: statement;
    font-size: 1.5rem;
}

.work-intro-statement p{
    letter-spacing: 0rem;
}

/* workimg */

.work-img--oversize{
    width: 100vw;
    margin-left: calc(-50vw + 50%);
}

.blank{
    margin-bottom: 50px;
}

.work-img--maxsize{
    width: 100%;
    margin: 30px 0;
}

.work-img--cardsize--wrap{
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 20px;
    margin: 30px 0;
}

.work-img--cardsize{
    height: 100%;
    object-fit: cover;
}

.textbox--bigsize{
    font-size: 2rem;
    letter-spacing: 0rem;
    margin: 30px 0 70px 0;
}

.textbox--middlesize{
    font-size: 1.2rem;
    letter-spacing: 0rem;
    margin: 30px 0 70px 0;
}

.work-return-button{
    font-size: 1.5rem;
    cursor: pointer;
    position: absolute;
    z-index: 1;
    color: #fff9f9;
    text-shadow:
        -1px -1px 0 #242424,
        1px -1px 0 #242424,
        -1px 1px 0 #242424,
        1px 1px 0 #242424;
    transition: .5s ease-in-out;
}

.work-return-button:hover{
    color: #242424;
    text-shadow:none;
}

.work-return-button--center-wrap{
    margin-top: -50px;
    text-align: center;
}

.work-return-button--center{
    font-size: 1.5rem;
    cursor: pointer;
    color: #fff9f9;
    text-shadow:
        -1px -1px 0 #242424,
        1px -1px 0 #242424,
        -1px 1px 0 #242424,
        1px 1px 0 #242424;
    transition: .5s ease-in-out;
}

.work-return-button--center:hover{
    color: #242424;
    text-shadow:none;
}

@media screen and (min-width:960px) {
    .work-intro {
        grid-template-columns: repeat(2, 1fr);
        grid-auto-rows: minmax(300px, auto);
        column-gap: 30px;
        grid-row-gap: 100px;
        grid-template-areas:
            "info img"
            "statement statement";
    }

    .work-intro-info{
        display: flex;
        justify-content: center;
        flex-direction: column;
        margin-top: 0;
    }

    .work-intro-info-details{
        position: absolute;
        bottom: 0;
    }
    /* imgbox */
    .blank{
        
    }

    .work-intro-statement {
        font-size: 3.5rem;
    }

    /* textbox */
    .textbox--bigsize{
        font-size: 6rem;
        margin: 50px 0;
    }

    .textbox--middlesize{
        font-size: 3rem;
        margin: 50px 0 150px 0;
    }
}